import { MouseEventHandler, PropsWithChildren, ReactNode } from "react"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Fab, useScrollTrigger } from "@rp/react/web/mui/core"

type Props = PropsWithChildren & {
  xPosition?: "right" | "left"
  Icon: ReactNode
  animateTo?: number
  onClick?: MouseEventHandler<HTMLButtonElement>
  yOffset?: number
  disabled?: boolean
  target?: Node | Window
  buttonVariant?: "default" | "text-outlined"
}

const FabExpanding = ({
  xPosition = "right",
  animateTo,
  children,
  Icon,
  onClick,
  yOffset = 0,
  disabled,
  target,
  buttonVariant = "default",
}: Props) => {
  const trigger = useScrollTrigger({ target })
  const xPos = xPosition === "right" ? { right: 16 } : { left: 24 }
  const { safeArea } = useDeviceInfo()

  return (
    <Fab
      color="primary"
      size="large"
      variant="extended"
      disabled={disabled}
      sx={theme => ({
        ...xPos,
        position: "fixed",
        bottom: `calc(24px + ${yOffset}px + ${safeArea.bottom})`,
        lineHeight: "14px",
        fontSize: theme.typography.body1.fontSize,
        textTransform: "none",
        transition: "0.1s width",
        height: 48,
        width: trigger ? 48 : animateTo ?? "auto",
        backgroundColor: buttonVariant === "text-outlined" ? "overlay.dragNDrop" : undefined,
        color: buttonVariant === "text-outlined" ? "primary.main" : undefined,
        fontWeight: buttonVariant === "text-outlined" ? 400 : undefined,
        "&.Mui-disabled": {
          backgroundColor: "elevation.dp06",
        },
      })}
      onClick={onClick}
    >
      <Box>{Icon}</Box>
      {!trigger ? (
        <Box sx={{ ml: 1, overflow: "hidden", whiteSpace: "nowrap" }}>{children}</Box>
      ) : null}
    </Fab>
  )
}

export { FabExpanding }
